body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
	background-color: #eaedf1;
}

.card{
	background: #fff;
 	border-radius: 2px;
 	box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  -webkit-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}
.card:hover {
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.group{
	padding: 8px;
}
/*Tower Forms*/
.towerForms{
	display: -webkit-flex;
	display: flex;
	padding-bottom: 30px;
	padding: 16px;
}

.towerForm{
	padding: 16px;
}

.towerForms.hidden{
	height: 0px;
}

.formHeader{
	padding: 10px 5px;
	font-weight: bold;
}

.towerForms, .towersWrapper{
	max-width: 2000px;
	margin-left: auto;
	margin-right: auto;
}
.towerForms .towerForm{
	padding: 10px;
	margin-top: 30px;
	padding: 20px;
	margin-left: 20px;
	margin-right: 20px;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

.input-group.date{
	position: relative;
	top: 50px;
	left: 5px;
}

.btn{
	background-color: #e74c3c;
	padding-top: 15px;
	padding-bottom: 15px;
	border: none;
	border-radius: 5px;
	color: white;
	font-weight: bold;
	padding-left: 30px;
	padding-right: 30px;
	cursor: pointer;
	display: block;
}
/*  MATERIAL DESIGN*/
* { box-sizing:border-box; }

/* basic stylings ------------------------------------------ */
.container 		{
  font-family:'Roboto';
  width:600px;
  margin:30px auto 0;
  display:block;
  background:#FFF;
  padding:10px 50px 50px;
}
h2 		 {
  text-align:center;
  margin-bottom:50px;
}
h2 small {
  font-weight:normal;
  color:#888;
  display:block;
}
.footer 	{ text-align:center; }
.footer a  { color:#53B2C8; }

/* form starting stylings ------------------------------- */
.group 			  {
  position:relative;
	display: inline-block;
  margin-bottom:25px;
	margin-top: 25px;
	width: 48%;
}
input 				{
  padding:10px 10px 10px 5px;
  display:block;
  width: 100%;
  border:none;
  border-bottom:1px solid #757575;
}

input[type=time]{
	position: relative;
	bottom: 2px;
}
input:focus 		{ outline:none; }

/* LABEL ======================================= */
label 				 {
  color:#999;
  font-size:18px;
  font-weight:normal;
  position:absolute;
  pointer-events:none;
  left:5px;
  top:10px;
  transition:0.2s ease all;
  -moz-transition:0.2s ease all;
  -webkit-transition:0.2s ease all;
}

/* active state */
input:focus ~ label, input:valid ~ label 		{
  top:-20px;
  font-size:14px;
  color:#5264AE;
}

/* BOTTOM BARS ================================= */
.bar 	{ position:relative; display:block; }
.bar:before, .bar:after 	{
  content:'';
  height:2px;
  width:0;
  bottom:1px;
  position:absolute;
  background:#5264AE;
  transition:0.2s ease all;
  -moz-transition:0.2s ease all;
  -webkit-transition:0.2s ease all;
}
.bar:before {
  left:50%;
}
.bar:after {
  right:50%;
}

/* active state */
input:focus ~ .bar:before, input:focus ~ .bar:after {
  width:50%;
}

/* HIGHLIGHTER ================================== */
.highlight {
  position:absolute;
  height:60%;
  width:100px;
  top:25%;
  left:0;
  pointer-events:none;
  opacity:0.5;
}

/* active state */
input:focus ~ .highlight {
  -webkit-animation:inputHighlighter 0.3s ease;
  animation:inputHighlighter 0.3s ease;
}

/* ANIMATIONS ================ */
@-webkit-keyframes inputHighlighter {
	from { background:#5264AE; }
  to 	{ width:0; background:transparent; }
}
@keyframes inputHighlighter {
	from { background:#5264AE; }
  to 	{ width:0; background:transparent; }
}


@media screen and (max-width: 1400px) {
	.group{
		width: 48%;
	}
	.towerForms, .towersWrapper{
		-webkit-flex-direction: column;
		        flex-direction: column;
	}

	.btn{
		display: inline-block;
	}

	.towersWrapper{
		font-size: 0.8em;
	}

	.towerList{
		min-height: 35px;
	}
	.towerList .listItem .deleteButton{
		width: 15px;
		height: 15px;
	}
}

@media screen and (max-width: 600px) {
	.group{
		width: 100%;
	}

	.input-group.date{
		position: relative;
		top: 0px;
		left: 0px;
	}

	.towersWrapper{
		font-size: 0.5em;
	}
	.towerList .listItem .deleteButton{
		width: 10px;
		height: 10px;
	}
}

.listItem{
	padding: 8px;
	display: -webkit-flex;
	display: flex;
	font-size: 0.8em;

}

.listItem .wo{
	width: 15%;
}

.listItem .pid{
	width: 10%;
}

.listItem .desc{
	width: 29%;
}

.listItem .time{
	width: 14%;
}

.listItem .exitTime{
	width: 14%;
}
.listItem .timer{
	width: 14%;
}

.deleteButton {
	content: url(/static/media/baseline-delete-24px.95cff60e.svg);
	width: 20px;
	height: 20px;
	position: relative;
	right: 0;
	cursor: pointer;
}

.tableContainer{
	background: #fff;
 	border-radius: 2px;
 	box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  -webkit-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
}

.towerTitle{
	background-color: #37474f;
	color: white;
	padding: 16px;
	border-radius: 100px;
	font-weight: bold;
	max-width: 120px;
	text-align: center;
	margin-bottom: 16px;
	margin-top: 16px;
}

.tableContainer:hover {
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
.listItem:nth-child(even){
	background-color:  #ecf0f1;
}
.listItem:nth-child(odd){
	background-color: white;
}
.listItem.outOfTime:nth-child(even){
	background-color: #e74c3c;
	color: white;
}
.listItem.outOfTime:nth-child(odd){
	background-color: #e3301c;
	color: white;
}
.listItem.outOfTime .deleteButton{
	content: url(/static/media/baseline-delete-white-24px.c195ea9b.svg);
}
.wo{
	width: 15%;
	display: inline-block;
}

.pid{
	width: 10%;
	display: inline-block;
}

.desc{
	width: 29%;
	display: inline-block;
}

.time{
	width: 14%;
	display: inline-block;
}

.exitTime{
	width: 14%;
	display: inline-block;
}
.timer{
	width: 14%;
	display: inline-block;
}


@media screen and (max-width: 600px) {
	.towerTitle{
		font-size: 0.5em;
		padding: 10px;
		max-width: 70px;
	}

	.tableContainer{
		font-size: 0.5em;
	}

	.listItem .deleteButton{
